<template>
    <div>        
        <div ref="swiperfull" class="swiper-full my-3">            
            <div class="swiper-slide" style="">
                <img :src="imageloaded"  :alt="imgLoaded" class="border-2 border-white" width="230px" height="157px">
            </div>                                    
        </div>  
        <div ref="swiper" class="swiper swiper-thumbs">
            <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(item, index) in images" :key="index">
                <img :src="item.img" :alt="'Sample' + index" class="border-2 border-white" width="230px" height="157px"
                @click="updImage(item.img)">
            </div>                        
            </div>     
            <div class="swiper-button-prev text-white"></div>
            <div class="swiper-button-next"></div>                        
        </div>
    </div>  
</template>
<script>
import Swiper, {Navigation, Pagination} from 'swiper'
import 'swiper/swiper-bundle.min.css'

export default {    
    name: 'GalleryComp',
    props: {
        images: {
            default: []
        },        
    }, 
    data() {
        return {
            imageloaded: ''
        }
    },  
    created() {         
        this.imageloaded = (this.images.length > 0) ? this.images[0].img : ''
    }, 
    mounted() {
        // const SECOND = 1000
        new Swiper(this.$refs.swiper, {      
            // Optional parameters
            modules: [Navigation, Pagination],
            // Optional parameters      
            slidesPerView: 5,      
            freeMode: true,
            watchSlidesProgress: true,
            autoplay: {
                delay: 1000,
            },      
            breakpoints: {                 
                480: {
                    slidesPerView: 5,                                          
                },
            },
            loop: false,                       
        })
    },  
    watch: {
        images(imgs) {
            this.imageloaded = (imgs.length > 0) ? imgs[0].img : ''
        }
    },    
    methods: {
        updImage(img) {
            this.imageloaded = img            
        }
    }
}
</script>
<style scoped>
    .swiper-thumbs .swiper-slide, .swiper-thumbs .swiper-slide img {
      width: 224px;
      height: 100px !important;
   }
   .swiper-full {
        width: 100%;
        height: 600px;
   }
   .swiper-full .swiper-slide, .swiper-slide img {
      width: 100%;
      height: 600px;
      /* height: 100px !important; */
   }
   [class*="swiper-button"] {
    color: white !important;
   }
   
</style>