<template>
   <div>
      <section class="md:flex md:flex-col gap-4 bg-white text-white pt-8">

         <h2 ref="buscador" class="uppercase text-3xl pt-6 text-blue font-bold
            md:text-6xl
            lg:text-4xl">
         - hotel -
         </h2>

         <span class="capitalize font-Clarissa text-blue text-5xl">{{country}}</span>

         <!-- <figure class="pt-16">
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure> -->

         <div class="flex flex-col items-center py-10 bg-blue">  
            <!-- BUSCADOR DE PRECIOS -->
            <form class="flex md:flex-row flex-col justify-center md:items-end gap-6 border border-white rounded-4xl py-6 px-3 m-auto md:text-xl mx-4 md:mx-auto md:w-11/12"
            @submit="searchPrices">                
                <div class="flex flex-col items-start gap-1 hotel-detail md:w-4/12 text-white">
                  <span class="font-semibold text-base text-left px-4">Rango de fecha a reservar</span>
                  <Datepicker 
                    class="datepicker-container full"
                    v-model="date" 
                    :format="'dd/MM/yyyy'"
                    range multi-calendars 
                    auto-apply
                    placeholder="Fecha de entrada y salida"
                    :enable-time-picker="false"                    
                    @update:modelValue="handleDate"
                    />
               </div>

               <!-- <stars :star_init="formData.stars" ></stars>     -->

               <div class="relative flex flex-col md:w-5/12">
                  <span class="px-4 text-base font-semibold leading-7 text-left">Habitaciones</span>
                  <div class="flex flex-col items-center justify-around px-4 py-1 text-white bg-transparent border border-white gap-x-2 rounded-4xl md:flex-row">  
                        <i class="fas fa-door-open"></i>
                        <span> {{noRooms}} Habitacion(es) </span>
                        <i class="fa fa-user"></i>
                        <span> {{numperson}} Persona(s) </span>
                        <div class="text-xl cursor-pointer text-orange md:ml-auto" @click="visible_person = !visible_person">
                           <i class="fas fa-edit"></i>
                        </div>                        
                  </div>
                  <div id="rooms" class="absolute right-0 z-10 flex px-3 bg-white gap-x-3 border-lightGray text-darkGray rounded-3xl"
                     :class="{'visible-numperson': visible_person}" >                            
                     <num-person 
                        :r="this.formData.rooms" 
                        @close="visible_person=false" 
                        @updRoomComb="updRoomComb"
                        @addRoom="updNumRooms" 
                        @subRoom="updNumRooms">
                     </num-person>
                  </div>                                   
               </div>

               <div class="flex gap-3 items-center md:w-1/12">
                  <button type="submit" class="px-4 py-1 text-xl text-white uppercase border bg-orange border-orange rounded-4xl hover:bg-transparent hover:text-orange">
                     buscar
                  </button>               
                  <i class="fa fa-spinner fa-pulse" v-show="loadingPrices"></i>
               </div>
            </form>
            
            <!-- DETALLE DEL HOTEL -->
            <div class="py-6 text-3xl" v-if="loading">
               <i class="fa fa-spinner fa-pulse"></i>
            </div>
            <div class="container flex flex-col gap-4 p-10 mx-auto md:flex-row" v-show="!loading">
               <!-- GALERIA DE IMAGENES -->
               <!-- <Gallery :images="hotelInfo.images" /> -->

               <!-- INFORMACION DEL HOTEL -->               
               <article class="flex flex-col items-start w-full gap-3 p-4 border-2 border-white border-dashed">
                  <div class="flex flex-col items-center justify-between w-full gap-2 md:flex-row md:gap-0">
                     <div class="flex flex-col items-center md:items-start">
                        <div class="flex flex-col gap-3 md:flex-row">
                           <h2 class="text-2xl uppercase font-DIN-Bold md:text-3xl">{{ hotelInfo.alias }}</h2>
                           <!-- Stars -->
                           <stars class="self-center md:ml-10" :star_init="hotelInfo.star"></stars>
                        </div>
                        <!-- Price From -->
                        <small class="my-5 md:my-0">
                           Desde 
                           <b class="text-xl text-orange font-DIN-Bold md:text-2xl ">${{ hotelInfo.price_from }}</b> 
                           {{ (hotelInfo.per_pax) ? '[por persona / la noche]' : '[por habiación]' }}
                        </small>       
                     </div>
                     <figure class="w-20">
                        <img class="border" :src="hotelInfo.location" alt="">
                     </figure>
                  </div>

                  <tabs class="w-full">
                     <tab name="Información General" class="px-4">
                        <!-- Description -->
                        <p class="py-4 text-left">{{ hotelInfo.description }} Lorem ipsum dolor sit amet consectetur adipisicing elit. Deleniti, ipsam molestias! Earum a molestiae dicta nostrum, molestias nisi, eum quia consectetur explicabo quos expedita! Et facere odio laudantium pariatur suscipit!</p>

                        <!-- CheckIn - Checkout -->
                        <section class="flex gap-3">
                           <b class="">Entrada: </b> {{ hotelInfo.check_in }}
                           <b class="">Salida: </b> {{ hotelInfo.check_out }}
                        </section>

                        <b class="border-b">Otras Características</b>
                        <article class="flex gap-3">
                           <h3 class="flex justify-between gap-4" v-if="(hotelInfo.buildings > 0)"><b>Edificio(s): </b> {{ hotelInfo.buildings }}</h3>
                           <h3 class="flex justify-between gap-4" v-if="(hotelInfo.houses > 0)"><b>Casa(s): </b> {{ hotelInfo.houses }}</h3>
                           <h3 class="flex justify-between gap-4" v-if="(hotelInfo.bungalows > 0)"><b>Bungalow(s): </b> {{ hotelInfo.bungalows }}</h3>
                        </article>
                        <b v-if="(hotelInfo.children_allowed > 0)">Permite niños</b>
                        <b v-if="(hotelInfo.children_allowed == 0)">No permite niños</b>

                        <hr class="w-full mt-3 border-dashed">  
                        <article class="flex flex-col w-full gap-2 md:flex-row">
                           <section class="flex flex-col items-start w-full gap-2 py-3 md:w-3/5">
                              <h3 class="uppercase "><b>Dirección</b></h3>
                              {{ hotelInfo.address }}
                           </section>
                           <section class="flex flex-col items-start w-full gap-2 py-3 md:w-3/5">
                              <h3 class="flex justify-between gap-4"><b class="uppercase">Teléfono(s) </b> {{ hotelInfo.phone }}</h3>
                              <h3 class="flex justify-between gap-4" v-if="hotelInfo.email"><b class="uppercase">Email(s) </b> {{ hotelInfo.email }}</h3>                              
                           </section>                           
                        </article>
                        <hr class="w-full mb-3 border-dashed"> 
                           
                        <!-- Precios -->
                        <article class="flex flex-col gap-3">
                           <h3 class="flex items-center gap-4 text-xl">
                              <b>Precios en el rango de fecha:</b>
                              <template v-if="formData.dataRange">
                                 <div>
                                    (<small>{{ (formData.dateRange) && formData.dateRange[0] }}</small>-<small>{{ (formData.dateRange) && formData.dateRange[1] }}</small>)                           
                                 </div>
                              </template>
                           </h3>
                           <div class="py-6 text-3xl" v-if="loadingPrices">
                              <i class="fa fa-spinner fa-pulse"></i>
                           </div>                  
                           <div class="w-full flex flex-col gap-3">    
                              <hotel-table-price 
                                 :borderType="'border border-solid'" 
                                 :rooms="comb_list" 
                                 @updRoomRequest="selectRoom"
                                 @scrollInto="$refs.buscador.scrollIntoView()">
                              </hotel-table-price>                                          
                              <small v-show="noPrices"  class="bg-orange text-base text-gray p-1">
                                 Debe escoger algun precio para enviar la solicitud. En caso que no salga ninguno pruebe con otro rango de fecha
                              </small>
                              <button class="uppercase font-DIN-Bold border border-white text-white rounded-75 px-8 py-1
                              hover:bg-white hover:text-blue ml-auto "
                              @click="sendRequest"
                              v-show="(comb_list.length > 0)">
                                 Enviar solicitud
                              </button>
                           </div>  
                        </article>
                     </tab>
                     <tab name="Galeria">
                        <Gallery v-if="hotelInfo.images" :images="hotelInfo.images" />
                     </tab>
                  </tabs>    
               </article>               
            </div>
         </div>
      </section>
   </div>
</template>
<script>
import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'

import moment from 'moment'

import Gallery from '@/components/Gallery.vue'
import NumPerson from '@/components/searcher/NumPerson'
import Stars from '@/components/searcher/Stars'
import HotelTablePrice from '@/components/product/HotelTablePrice'
import Authorization from '@/services/user/auth.service'

import {Tabs, Tab} from 'vue3-tabs-component'
import '@/assets/css/tabs.css'

const SLIDE_CONFIG = {                                                          
      direction   : 'ttb',
      rewind      : true,
      wheel       : true,
      pagination  : false,
      autoplay    : true,
      arrows      : false,
      perPage     : 3,                  
      height       : '50rem',
      width       : '20rem',
      fixedWidth  : '25rem',
      fixedHeight  : 'auto',
      breakpoints: {         
         640: {
            direction   : "ltr",
            width       : "100%",
            height      : "auto",
            fixedWidth  : "30rem",
            fixedHeight : "10rem",
            perPage     : 1,
         }
      }                     
}

export default {
   name: 'ProductsDetail',   
   components: {
      Datepicker,
      NumPerson,
      HotelTablePrice,
      Stars,
      Tabs, Tab,
      Gallery      
      // map
   },   
   data() {
      return { 
         slide_options: SLIDE_CONFIG,                 
         loading: false,
         loadingPrices: false,
         hotel_id: 0,
         visible_person: false,         
         hotelInfo: {},    
         noRooms: 1,     
         country: '', 
         noPrices: false,
         request_obj: {},
         date: {},
         comb_list: [],
         formData: {
               rooms: [{
                  adults: 1,
                  children: 0,
                  infants: 0,
               }],
               dateRange: {
                  startDate: '',
                  endDate: ''
               },  
               destination: {
                  value: '',
                  label: ''
               },    
               hotel: {
                  value: '',
                  label: ''
               }, 
               stars: 3           
         },  
         //map
         // center: latLng(47.41322, -1.219482),
         // attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
         // withPopup: latLng(47.41322, -1.219482),
         // withTooltip: latLng(47.41422, -1.250482),
         // currentZoom: 11.5,
         // currentCenter: latLng(47.41322, -1.219482),
         // showParagraph: false,
         // mapOptions: {
         // zoomSnap: 0.5
         // },
         // showMap: true
      }
   },
   computed: {
      numperson() {
         let noPerson = 0
         for(let r of this.formData.rooms) {                
               noPerson += r.adults + r.children
         }
         return noPerson
      }, 
   },
   created() {    
      this.country =  this.$route.params.poleKey 
      this.getHotelinfo()
   },
   methods: {
      async getHotelinfo() {         
         // const am = 'iron'
         // const amenities = [
         //    require("@/assets/img/amenities/" + am + ".png")
         // ]
         const hotel_keyword = this.$route.params.keyword      
         // Buscando informacion del hotel         
         this.loading = true
         this.hotelInfo.images = [
            // {img: require("@/assets/img/block1.png")},
            // {img: require("@/assets/img/block2.png")},
            // {img: require("@/assets/img/block3.png")},
            // {img: require("@/assets/img/block2.png")},
            // {img: require("@/assets/img/block1.png")},
         ] 
         await Authorization.checkLogin(`/hotels/${hotel_keyword}`).then(e => {                        
            this.loading = false            
            if(e) {                                                                      
               this.hotelInfo = e.data                    
               this.hotelInfo.location = require(`@/assets/img/segment/${this.hotelInfo.hotel_location.keyword}.png`)    
               if(this.hotelInfo.images.length > 0) {
                  // this.hotelInfo.images = this.getImages(this.hotelInfo.images, 'thumbnail_w190xh267')                  
                  this.hotelInfo.images = this.getImages(this.hotelInfo.images, 'original')                  
               } else                   
                  if(this.hotelInfo.images.length < 3) {
                     this.slide_options.perPage = this.hotelInfo.images.length
                  }
            }   
                     
         })
      },     
      updRoomComb(obj) {
         this.formData.rooms[obj.index] = obj.comb         
      },      
      updNumRooms(obj) {
         // console.log(obj)
         if(obj.action == 'add') {
               this.formData.rooms.push({
                  adults: 1,
                  children: 0,
                  infants: 0,
               })
         } else if (obj.action == 'del') {
               this.formData.rooms.splice(obj.pos, 1)
         }
         this.noRooms = this.formData.rooms.length                            
      },
      searchPrices(e) {
         e.preventDefault()
         // Searching by params     
         const search_params = {
            hotel_id: this.hotelInfo.id,	
            date_from: this.formData.dateRange.startDate,
            date_to: this.formData.dateRange.endDate,
            pole_id: this.hotelInfo.pole_id,
            rooms: this.formData.rooms
         }
         this.loadingPrices = true
         search_params.auth = 'bearer'
         
         Authorization.checkLogin('/search/hotels/prices', search_params, 'post').then(e => {
            this.loadingPrices = false
            this.product = 'hotel',
            this.country = 'cuba'
            let prices = {}
            if(e) {                
               if(e.data.length > 0) { 
                  prices = e.data[0]     
                  // console.log(this.prices)      
                  this.request_obj = {
                     country: this.country,
                     pole: this.hotelInfo.pole.alias,
                     date: moment(this.formData.dateRange[0]).format('YYYY-MM-DD') + '/' + moment(this.formData.dateRange[1]).format('YYYY-MM-DD'),
                     product: 'Hoteles',
                     product_obj: {
                        name: this.hotelInfo.alias
                     },
                     id: prices.id,             
                     is_home_request: false  
                  }     
                  this.comb_list = []             
                  // parseo las habitaciones por combinaciones de cada hotel
                  this.formData.rooms.map((e,index) => {            
                     if(this.comb_list.length-1 < index) {
                        this.comb_list.push([])
                     }          
                     if('rooms' in prices) {
                        for(let r of prices.rooms) {
                           if(r.combination) {
                              r.combination.map(c => {
                                 if(c[0].adults_quantity == e.adults &&
                                    c[0].children_quantity == e.children &&
                                    c[0].infants_quantity == e.infants) {
                                    this.comb_list[index].push({
                                       room_id: r.room_id,
                                       room: r.name,
                                       prices: r.total_prices[index],
                                       num_night: c.length-1,
                                       comb: e
                                    })
                                 }
                              })
                           }
                        }
                     }
                  })                  
               } 
            }
         })
      },
      sendRequest() {  
         if('product_obj' in this.request_obj) {            
            localStorage.removeItem('request_info')
            // verifico que se haya escogido alguna habitacion                      
            if('rooms' in this.request_obj.product_obj) {
               let total_price = 0,
                  total_person = 0,
                  total_room = 0
               const rooms = []
               // Sacando los totales de personas, precio y cant de habitaciones
               total_room = this.request_obj.product_obj.rooms.length
               for(let r of this.request_obj.product_obj.rooms){
                  total_person += r.comb.adults + r.comb.children + r.comb.infants
                  total_price += r.price

                  rooms.push({
                     type: r.name,
                     comb: this.getCombination(r.comb)
                  })
               }
               const request = {
                  type: 'hotel',
                  destination: this.request_obj.country,
                  product: this.request_obj.product,
                  country: this.request_obj.country,
                  pole: this.request_obj.pole,
                  date: this.request_obj.date,
                  product_name: this.request_obj.product_obj.name,
                  price: total_price,
                  num_person: total_person,
                  num_rooms: total_room,
                  is_home_request: false,
                  rooms                           
                  }  
                  localStorage.setItem('request_info', JSON.stringify(request))           

               console.log(request)
               // this.$router.push({name: 'SendRequest'})
            } else {
               this.noPrices = true
               setTimeout(() => {                            
                  this.noPrices = false
               }, 3000);
            }
         } 
      },
      selectRoom(rooms) {
         if('product_obj' in this.request_obj) {
            this.request_obj.product_obj.rooms = rooms
         }
         // console.log(this.request_obj)
      },
      getImages(img_list, filter = 'original') {
         const images = []
         img_list.map(i => {
            const img = i.resources.filter(item => item.image == filter)
            if(img) {
               images.push({img: img[0].url})
            }
         })
         return images
      },
      getCombination(r) {
         let comb = ''
         comb += (r.adults) ? r.adults + ' Adulto(s)' : ''
         let ninos = (r.children) ?  r.children + ' Niño(s)' : ''
         if(ninos)
            comb += (comb) ? ',' + ninos : ninos
         let infant = (r.infants) ?  r.infants + ' Infantes(s)' : ''
         if (infant)
            comb += (comb) ? ',' + infant : infant
         return comb
      },    
      handleDate(range_date) {
         this.formData.dateRange.startDate = moment(range_date[0].toString()).format('YYYY-MM-DD')
         this.formData.dateRange.endDate = moment(range_date[1].toString()).format('YYYY-MM-DD')
      },        
   }
}
</script>
<style scoped>     
   .title {
      text-transform: uppercase;
      font-family: 'DINPro-Bold';
      font-size: 18px;
      margin-bottom: 5px;
   }

    #rooms {
        visibility: collapse;
        opacity: 0;
        top: 70px;
        padding-top: 0.5rem;    
        padding-bottom: 0.5rem;    
        border: 1px solid var(--light-gray);
    }

    .visible-numperson {                
        visibility: visible !important;
        opacity: 1 !important;
        transition: opacity 150ms;        
        transition-timing-function: cubic-bezier(.22,.48,.82,.61);
    }
    @media screen and (min-width: 768px) {
        .visible-form {
            height: 260px !important;
        }    
    }
</style>